*{
    margin: 0px;
    padding: 0px;   
} 
html {
  font-size: 62.5%;
  box-sizing: border-box;


.contenedor{
      max-width: 120rem;
      margin: 0 auto;
  }
.logo{
  display: block;
  width: 15%;
}
.encabezado{
  padding-bottom: 42rem;
  background-image: url(fondo1.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
    @media (max-width: $mq) {
      background-size: cover;
      padding-bottom: 20rem;
      background-image: url(fondo1.jpg);
      background-size: cover;
      position: relative;
}
}
.header{
  display: flex;
  justify-content: center;
  background-color: $azulOscuro;
  padding-top: 0.9rem;
    @media (max-width: $mq){
  display: flex;
  justify-content: center;
  background-color: $azulOscuro;
  padding-top: 0.9rem;
  }
    img.logo__2{
    display: block;
    height: 40%;
    margin-left: auto;
    margin-right: auto;
  }
}


body {
  font-size: 30px;
 font-family: "Krub", sans-serif;
  /*1rem=10px*/
  background-image: linear-gradient(to top,$grisClaro 0%, $blanco 100%);
    .nav-bg {
    background-color: $gris;
    @media (max-width: $mq) {
    display: flex;
    flex-direction: column;
}
}

.navegacion-principal {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 7rem;
  margin-right: 7rem;
     @media (max-width: $mq) {
    flex-direction: column;
    justify-content: space-between;
}

    .indice{
  display: block;
  text-align: center;
  color: $blanco;
  text-decoration: none;
  font-size: 1.5rem;
  font-weight: 500;
  padding: 1.5rem;
      &:hover{
        background-color: $azulClaro;
        color: $oscuro;
        font-size: 1.6rem;
      }
}
}

.productos{
  background-color: $oscuro;
  @include mx1;

.grid{
  display: grid ;
  text-align: center;
  justify-content: center;
  grid-template-columns: repeat(3, 1fr);
  gap: 8rem;
  margin: 4rem;
    @media (max-width: $mq) {
        grid-template-columns: repeat(1, 1fr);
    }
}

}
.producto{
    background-image: linear-gradient(to top, $rojo 0%, $azulOscuro 100%);
    padding: 1rem;
}
.producto__a{
  color: $oscuro;
  text-decoration: none;
    &:hover{
    cursor: pointer;
    color: $blanco;
    }
}
.producto__nombre
.producto__precio{
    font-size: 20%;
    margin: 1rem 0;
    text-align: center;
    line-height: 1;
}    

.producto__info{
  font-size: 70%;
  margin-top: 1rem;
}



.contenedor{
  background-color: $grisClaro;

.h1Remera{
      text-align:center;
      color: azulOscuro;
      padding: 3rem;
      margin-bottom: 2rem;
  }
.imgProd{
    display: block;
    margin: auto;
    }

  .camisa{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2rem;
    font-size: 2rem;
    text-align: center;
    padding-bottom: 8rem;
      @media (max-width: $mq) {
        display: block;
        justify-content: center;
        column-gap: 2rem;
    
    }
.descripcionRem{
  margin: 2rem;
}
.precio{
  margin: 4rem;
}

  .formularioProd {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    margin-bottom: 5rem;
     @media (max-width: $mq) {
     display: block;
     text-align: center;
     column-gap: 3rem;
     }

.formulario__campo {
    margin: inherit;
    border: 1rem solid $azulOscuro; 
    background-color: transparent;
    color: black;
    font-size: 2rem;
    padding: 1rem;
    appearance: none;

      &:hover{
      cursor: pointer;
      border: 1rem solid $rojo; 
      }
    }

 .formulario__submit {
    background-color: $gris;
    border: none;
    font-size: 2rem;
    padding: 2rem;
    transition: font-size .5s ;
    display: block;
    align-items: center;
    grid-column: 1/ 3; 
    @media (max-width: $mq) {
    display: initial;
    margin-top: 3rem;
    }
      &:hover {
    font-size: 2.4rem;
    cursor: pointer;
    color: $blanco;
    }
    }
    }

   }
   }


.creaCuenta{
  background-color: $rojoClaro;
  justify-content: center;
  text-align: center;
  margin-top: 5rem;
  margin-bottom: 2rem;
    .cuenta{
  text-align: center;
  font-weight: 500;
  font-size: 50%
}
}



.formBT{
  background-color: $azulClaro;
  width: 45rem;
  margin: 0 auto;
  margin-top: 6rem;
  margin-bottom: 4rem;
  border-radius: 1rem;
    @media (max-width: $mq) {
    width: 40rem;
    }
    .form-label{
  font-size: 1.5rem;
  margin-left: 2rem;
  }
    .form-control{
    margin-left: 1rem;
    width: 95%;
    border: none;
    font-size: 50%;
    margin-left: 1rem;
}
.form-text{
  font-size: 1.5rem;
  text-align: center;
  padding: 0.5rem;
}

.form-check{
  padding-left: 1rem;
  }
    .form-check-input{
      padding-left: 0.5rem;
      }
      .form-check-input[type=checkbox] {
        margin-left: 1rem;
        }
        .form-check-label{
          font-size: 1.5rem;
          margin: 1rem;
}
}

.btn /*btn-primary*/{
  margin: 2rem;
  text-decoration: none;
  text-align: center;
  font-size: 40%;
  border: none;
  padding: 0.8rem;
}

.nosotros{
    display: grid;
    grid-template-columns:repeat(2, auto); ;
    padding: 3rem;
    column-gap: 3rem;
    justify-content: center;
    text-align: center;
    font-size: 2rem;
    margin-bottom: 5rem;
    
      @media (max-width: $mq) {
    grid-template-columns: min-content;
    column-gap: 2rem;
    text-align: center;
}

}
.texto-nosotros{
  margin-left: 3rem;
  padding: 3rem;
  margin-bottom: 1rem;
    @media (max-width: $mq) {
    margin-top: 9rem;
}
}

.logo__2{
margin: 3rem 0;
}
.pq__nosotros{
  margin: 5rem;
  font-size: 100%;
  background-color: #FFDBDC;
  padding: 3rem;
  border-radius: 1.5rem;
}


.nosotros__end{
  text-align:center;
  font-family: "Krub", sans-serif;
}
.textoFinalNosotros{
   display: grid;
   grid-template-columns:  repeat(3, 1fr);
   gap: 2rem;
   justify-content: center;
   text-align: center;
   font-size: 80%;
   margin: 5rem;
   column-gap: 5rem;
    @media (max-width: $mq) {
        grid-template-columns: repeat(1, 1fr);
        justify-content: center;
        text-align: center;
    
}

.elPQ{
     -webkit-box-shadow: 0px 5px 15px 0px rgba(99, 99, 99, 0.54);
    -moz-box-shadow: 0px 5px 15px 0px rgba(99, 99, 99, 0.54);
    box-shadow: 0px 5px 15px 0px rgba(99, 99, 99, 0.54);
    background-color: var(--blanco);
    padding: 2rem;
    border-radius: 1rem;
    transition: font-size .5s;
      &:hover{
      cursor:pointer;
      background-color: #E0E9FC;
      font-size: 2.1rem;

      }
.size--nosotros{
  font-size: 80%;
}      
}
}
.pq__nosotros{
  color: $azulOscuro;
}
.carrousel{
  justify-content: center;
 width: 340px; 
 height: 160px; 
}

.direccion{
  text-align: center;
  margin-top: 5rem;
  font-size: 85%;
}
.mapa{
  display: flex;
  justify-content: center;
  margin: 5rem;
}
.horarios{
  display: block;
  text-align: center;
  .atencion{
  color: $rojo;
  margin-top: 2rem;
  margin-bottom: 4rem;
  .atencionDia{
  font-size: 2.5rem;
  margin-bottom: 5rem;
}
}
}

.formulario{
.form{
  background-color: $rojo;
  width: 60rem;
  margin: 0 auto;
  margin-top: 6rem;
  margin-bottom: 4rem;
  border-radius: 1rem;
  border: none;
  background-image: linear-gradient(to right,$rojo 0%, $azulOscuro 100%);
    @media (max-width: $mq) {
    box-sizing: border-box;
    justify-content: center;
}
.legend {
  padding-top: 8rem;
  text-align: center;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 2rem;
  color: $blanco;
}
.letraForm{
  margin-bottom: 1rem;
  margin-left: 2rem;
  font-size: 2rem;
    .txt {
    color: $blanco;
    font-weight: bold;
    margin-bottom: 0.5rem;
    display: block;
    margin: 1rem;
}
}


.input-text{
  width: 90%;
  border: none;
  padding: 2rem;
  border-radius: 0.5rem;
  border-style: inherit;
}

  .boton{
  background-color: $gris;
  color: $blanco;
  padding: 1rem 3rem;
  margin-top: 3rem;
  font-size: 2rem;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bold;
  border-radius: 0.5rem;
  width: auto;
  text-align: center;
  border: none;
  display:flex;
  justify-content: flex-end;
  margin-left: 44.8rem;
  margin-bottom: 1.5rem;
    &:hover {
    cursor: pointer;
    background-color: #E1CDCD;
}
}
}
}
  footer{
  font-size: 2.1rem;
  text-align: center;
  padding: 4rem;
  margin-top: 5rem;
}
}
}
